.questionCard{
    position: relative;
    column-count: 2;
    width: 70%;
    height: 40vh;
    padding: 30px;
    margin-top: 5%;
    margin-left: 15%;
    background-color: beige;
    display: grid;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
    font-family: sans-serif;
    font-size: 2rem;
    box-shadow: 0 5px 10px 0 #00000040, 0 5px 20px 0 #00000080;
}